import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { CollectData } from '../api/ConnectApi'
import { Link } from 'react-router-dom'
import Footer from '../Footer/index'
import './btn.css'
import { Helmet } from 'react-helmet'

//Here is the topic given is constant
export const RandomExams = () => {
  const { topic } = useParams()
  // console.log(topic)
  const API_URL = 'http://localhost:8000/api/exams/r/' + topic + "/"
  const [dataState] = CollectData(API_URL)
  // console.log(dataState)
  const [answer, setAnswer] = useState({})
  const [answerCheck, setAnswerCheck] = useState()
  const a = dataState.data.flatMap((ex) => ex.answer)
  const ac = a.length
  const handleChange = (e) => {
    setAnswer({ ...answer, [e.target.value]: e.target.checked })
  }

  useEffect(() => {
    if (Object.keys(answer).length === 0) {
      setAnswer(createInitialanswers())
    }
  }, [answer])
  const createInitialanswers = () => {
    let z = a.flatMap((obj) => obj.id)
    var object = {}
    for (var x = 0; x < ac; x++) {
      object[z[x]] = false
    }
    return object
  }
  const checkAnswer = (e) => {
    e.preventDefault()
    let n = a.map((obj) => obj.is_right)
    let y = { ...n }

    function arrayEquals(o, p) {
      return (
        Array.isArray(o) &&
        Array.isArray(p) &&
        o.length === p.length &&
        o.every((val, index) => val === p[index])
      )
    }
    let o = Object.values(y)
    let p = Object.values(answer)
    if (arrayEquals(o, p)) {
      setAnswerCheck(true)
    } else {
      setAnswerCheck(false)
    }
  }

  function refreshPage() {
    window.location.reload(false)
  }

  // const choice = ['A', 'B', 'C', 'D']
  const Result = () => {
    if (answerCheck === true) {
      return (
        <div class='alert alert-success'>
          <h2>Well Done!</h2>
          <br />
          <p>You are good on it</p>
          <Link href='#' onClick={refreshPage}>
            {'Next Question'}
          </Link>
        </div>
      )
    } else if (answerCheck === false) {
      return (
        <div class='alert alert-danger'>
          <h2>Wrong Answer!</h2>
          <p>Please read on it!</p>
          <Link href='#' onClick={refreshPage}>
            {'Next Question'}
          </Link>
        </div>
      )
    } else {
      return <React.Fragment></React.Fragment>
    }
  }

  return (
    <>
      <Helmet>
        <title>Model Exam</title>
      </Helmet>
      <div
        class='container-fluid'
        maxWidth='xs'
        style={{
          display: 'flex',
          justifyContent: 'Right',
          alignItems: 'Right',
          height: '100vh',
        }}
      >
        <h1>
          <b>
            <u>University Entrance Exam</u>
          </b>
        </h1>
        <hr />
        <hr />
        <hr /> <hr /> <hr /> <hr />
        <div class='row'>
          {dataState.data.map(({ title, answer }, i) => (
            <div key={i}>
              <hr /> <hr /> <hr /> <hr /> <hr /> <hr />
              <h1>
                Q.
                <b>{title}</b>
              </h1>
              {answer.map(({ answer_text, id }) => (
                <form>
                  <div class='container'>
                    <div class='form-group'>
                      <input
                        id='checkbox2'
                        class='styled'
                        type='checkbox'
                        value={id}
                        onChange={handleChange}
                      />
                      <label>
                        <h5>{answer_text}</h5>
                      </label>
                    </div>
                  </div>
                </form>
              ))}
              <button
                type='submit'
                class='btn btn-default'
                onClick={checkAnswer}
              >
                Submit
              </button>
              <Result />
            </div>
          ))}
        </div>
      </div>
      <Footer />
    </>
  )
}
