import React, { useState, useContext } from 'react'
import { BoxContainer } from '../AccountBoxElement/AccountBoxElement'
import { Button } from '../ButtonElements'
import { CollectData } from '../api/ConnectApi'
import { InsertData } from '../api/ConnectApi'
import { FormContainer } from '../AccountBoxElement/Common'
import { Link, Redirect } from 'react-router-dom'
import APIService from '../api/APIService'

export const ExamForm = () => {
  const [formData, setFormData] = useState({
    title: '',
    name:'',
  })
  const API_URL = 'http://localhost:8000/api/exams/catagory/'
  const [dataState] = CollectData(API_URL)
  console.log(dataState)
  const { title, name } = formData

  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
    console.log(e.target.value)
  }
   const insertExam = () => {
     APIService.insertExam({ name, title }).then((resp) =>
       console.log('Inserted')
     )
   }
  // const onSubmit = (e) => {
  //   e.preventDefault()
  //   InsertData(title)
  // }
  //Is the user authenticated? then redirect to the home page of the user

  return (
    <>
      <FormContainer
        className='events'
        style={{
          display: 'flex',
          justifyContent: 'Right',
          alignItems: 'Right',
          height: '100vh',
          width: '100vh',
        }}
      >
        <input
          type='text'
          placeholder='Title'
          name='title'
          value={title}
          onChange={(e) => onChange(e)}
          required
        />
        <hr />
        <select className='form-control' onChange={(e) => onChange(e)}>
          <option>---select exam catagory---</option>
          {dataState.data &&
            dataState.data.map((h, i) => (
              <option key={i} value={h.name}>
                {h.name}
              </option>
            ))}
        </select>
        <hr /> <hr /> <hr /> <hr /> <hr /> <hr />
        <hr /> <hr />
        <Button
          type='submit'
          onClick={insertExam}
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '10vh',
            width: '20vh',
          }}
        >
          INSERT
        </Button>
      </FormContainer>
    </>
  )
}
