import React from 'react'
import { logout } from '../../../actions/auth'
import {
  Nav,
  NavLink,
  Bars,
  NavMenu,
  NavBtn,
  NavBtnLink,
} from './NavbarElements'

const Navbar = ({logout}) => {
  return (
    <>
      <Nav>
        <Bars />
        <NavMenu>
          <NavLink to='/schoole/face' activeStyle>
            Profile
          </NavLink>
          <NavLink to='/schoole/events' activeStyle>
            Events
          </NavLink>
          <NavLink to='/schoole/annual' activeStyle>
            Annual Report
          </NavLink>
          <NavLink to='/schoole/team' activeStyle>
            Teams
          </NavLink>
          <NavLink to='/schoole/blogs' activeStyle>
            Blogs
          </NavLink>
          <NavLink to='/schoole/sign-up' activeStyle>
            Menu
          </NavLink>
          {/* Second Nav */}
          {/* <NavBtnLink to='/sign-in'>Sign In</NavBtnLink> */}
        </NavMenu>
        <NavBtn>
          <NavBtnLink to='/schoole/logout'>Sign Out</NavBtnLink>
          {/* <a
            class='nav-link active'
            aria-current='page'
            href='#!'
            onClick={logout}
          >
            <NavBtnLink to='/logout'>
            Logout
            </NavBtnLink>
          </a> */}
          {/* </NavBtnLink> */}
        </NavBtn>
      </Nav>
    </>
  )
}

export default Navbar
