import './index.css'
import Sidebar from './Sidebar/Sidebar'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { AboutUs, OurAim, OurVision } from './pages/AboutUs'
import About  from './Npages/about'
import Event from './Npages/events'
import AnnualReport from './Npages/annual'
import Teams from './Npages/team'
import { CatagoryForm } from '../ModelExam/catagoryForm'
import SignUp from './Npages/signup'
import {
  Services,
  ServicesOne,
  ServicesTwo,
  ServicesThree,
} from './pages/Services'
import { Events, EventsOne, EventsTwo } from './pages/Events'
import Contact from './pages/ContactUs'
import Support from './pages/Support'
import {RandomExams} from '../ModelExam/RandomExams'
import { ExamForm } from '../ModelExam/examForm'
import { QuestionForm } from '../ModelExam/questionForm'
import {Play} from '../Exam/Play'
import { AnswerForm } from '../ModelExam/answerForm'
import HeroSection from '../HeroSection/index'
import Article from '../ArticleList'
import Footer from '../Footer/index'
import { logout } from '../../actions/auth'
export const Index = () => {
  return (
    <Router>
      <Sidebar />
      <Switch>
        <Route path='/schoole/about-us' exact component={AboutUs} />
        <Route path='/schoole/about-us/aim' exact component={OurAim} />
        <Route path='/schoole/about-us/vision' exact component={OurVision} />
        <Route path='/schoole/services' exact component={Services} />
        <Route
          path='/schoole/services/services1'
          exact
          component={ServicesOne}
        />
        <Route path='schoole/services/services3/exam' exact component={Play} />
        <Route
          path='/schoole/services/services3'
          exact
          component={ServicesThree}
        />
        <Route path='/schoole/contact' exact component={Contact} />
        <Route path='/schoole/events' exact component={Events} />
        <Route path='/schoole/events/events1' exact component={CatagoryForm} />
        <Route path='/schoole/events/events2' exact component={ExamForm} />
        <Route path='/schoole/events/question' exact component={QuestionForm} />
        <Route path='/schoole/events/answer' exact component={AnswerForm} />
        <Route path='/schoole/support' exact component={Support} />
        <Route
          path='localhost:3000/api/exams/r/:topic'
          component={RandomExams}
          exact
        />
        <Route path='/schoole' exact component={HeroSection} />

        <Route path='/schoole/face' exact component={About} />
        <Route
          path='/schoole/
        events'
          component={Event}
        />
        <Route path='/schoole/annual' component={AnnualReport} />
        <Route path='/schoole/team' component={Teams} />
        <Route path='/schoole/blogs' component={Article} />
        <Route path='/schoole/sign-up' component={SignUp} />
        <Route path='/schoole/logout' component={logout} />
      </Switch>
      <Footer />
    </Router>
  )
}


