import React, { useState, useContext } from 'react'
import { BoxContainer } from './AccountBoxElement'
import {
  BoldLink,
  FormContainer,
  Input,
  MutedLink,
  SubmitButton,
} from './Common'
import { AccountContext } from './accountContext'
import { Link, Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { register } from '../../actions/auth'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
// import { useCookies } from 'react-cookie'

export const SignUpForm = ({ register, isAuthenticated }) => {
  // const [catagory, setCatagory] = useState('')
  const [value, setValue] = React.useState('student')
  const handleChange = (event) => {
    setValue(event.target.value)
    console.log(value)
  }
  const [accountCreated, setAccountCreated] = useState(false)
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    first_name: '',
    last_name: '',
    phone: '',
    password: '',
    re_password: '',
    role: 'student',
  })
  const { switchToSignin } = useContext(AccountContext)
  const {
    username,
    email,
    first_name,
    last_name,
    phone,
    password,
    re_password,
    role,
  } = formData

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value })
    console.log()
  const onSubmit = (e) => {
    e.preventDefault()
    //Registration
    if (password === re_password) {
      register(
        username,
        email,
        first_name,
        last_name,
        phone,
        password,
        re_password,
        role
      )
    }
    setAccountCreated(true)
  }
  //Is the user authenticated? then redirect to the home page of the user

  if (isAuthenticated) {
    return <Redirect to='/schoole' />
  }
  if (accountCreated) {
    return <Redirect to='/' />
  }

  return (
    <>
      <BoxContainer>
        {/* onSubmit={(e) => onSubmit(e)} */}
        <FormContainer onSubmit={(e) => onSubmit(e)}>
          <Input
            type='text'
            placeholder='User name'
            name='username'
            value={username}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='text'
            placeholder='Enter First name'
            name='first_name'
            value={first_name}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='text'
            placeholder='Last name'
            name='last_name'
            value={last_name}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='email'
            placeholder='Email'
            name='email'
            value={email}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='text'
            placeholder='Phone number'
            name='phone'
            value={phone}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='password'
            placeholder='Password'
            name='password'
            value={password}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='password'
            placeholder='Confirm Password'
            name='re_password'
            value={re_password}
            onChange={(e) => onChange(e)}
            required
          />
          <FormLabel component='legend'>
            <u>
              <b>Role</b>
            </u>
          </FormLabel>
          <RadioGroup
            aria-label='gender'
            name='role'
            value={value}
            onChange={handleChange}
          >
            <div
              style={{
                display: 'flex',
              }}
            >
              <FormControlLabel
                value='student'
                control={<Radio />}
                label='Student'
              />
              <FormControlLabel
                value='teacher'
                control={<Radio />}
                label='Teacher'
              />
              <FormControlLabel
                value='parent'
                control={<Radio />}
                label='Parent'
              />
            </div>
          </RadioGroup>
          <hr />
          <SubmitButton type='submit'>Signup</SubmitButton>
        </FormContainer>
        <MutedLink href='#'>
          Already have an account?
          <BoldLink href='#' onClick={switchToSignin}>
            Signin
          </BoldLink>
        </MutedLink>
      </BoxContainer>
    </>
  )
}

const mapStateToProps = (state) => ({
  //Is authenticated?
  isAuthenticated: state.auth.isAuthenticated,
})
const SignUps = connect(mapStateToProps, { register })(SignUpForm)
export default SignUps
