import React, { useState, useContext } from 'react'
import { BoxContainer } from './AccountBoxElement'
import {
  BoldLink,
  FormContainer,
  Input,
  MutedLink,
  SubmitButton,
} from './Common'
import { AccountContext } from './accountContext'
import { Link, Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { signin } from '../../actions/auth'

const LogInForm = ({ signin, isAuthenticated }) => {
  // const [username, setUsername] = useState('')
  // const [password, setPassword] = useState('')
  // const [token, setToken] = useCookies(['csrftoken'])
  const { switchToSignup } = useContext(AccountContext)
  // let history = useHistory()
  const [formData, setFormData] = useState({
    username: '',
    password: '',
  })

  const { username, password } = formData

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value })

  const onSubmits = (e) => {
    e.preventDefault()
    //Login
    console.log(username, password)
    signin(username, password)
  }
  //Is the user authenticated? then redirect to the home page of the user

  if (isAuthenticated) {
    return <Redirect to='/schoole' />
  }
  console.log(isAuthenticated)
  // console.log(username)
  // console.log(password)

  // useEffect(() => {
  //   if (token['csrftoken'] && token['csrftoken'] !== 'undefined') {
  //     history.push('/schoolInformation')
  //   }
  // }, [token])

  // const loginBtn = () => {
  //   APIService.LoginUser({ username, password })
  //     .then((resp) => setToken('csrftoken', resp.token))
  //     .catch((error) => console.log(error))
  // }

  return (
    <>
      <BoxContainer>
        <FormContainer onSubmit={(e) => onSubmits(e)}>
          <Input
            type='text'
            placeholder='User Name'
            name='username'
            value={username}
            onChange={(e) => onChange(e)}
            required
          />
          <Input
            type='password'
            placeholder='Password'
            name='password'
            value={password}
            onChange={(e) => onChange(e)}
            minLength='8'
            required
          />
          <hr className='hrclass' />
          <SubmitButton type='submit'>Signin</SubmitButton>
        </FormContainer>
        <MutedLink href='#'>Do U forget your password?</MutedLink>
        <MutedLink href='#'>
          Don't have an account?
          <BoldLink href='#' onClick={switchToSignup}>
            Signup
          </BoldLink>
          <p className='mt-3'>
            Forgot password?<Link to='/reset-password'>Reset password</Link>
          </p>
        </MutedLink>
      </BoxContainer>
    </>
  )
}
const mapStateToProps = (state) => ({
  //Is authenticated?
  isAuthenticated: state.auth.isAuthenticated,
})

const LogIns = connect(mapStateToProps, { signin })(LogInForm)
export default LogIns;
