import React, { useState, useContext } from 'react'
import { BoxContainer } from '../AccountBoxElement/AccountBoxElement'
import { Button } from '../ButtonElements'
import { CollectData } from '../api/ConnectApi'
import { InsertData } from '../api/ConnectApi'
import { FormContainer } from '../AccountBoxElement/Common'
import { Link, Redirect, useParams } from 'react-router-dom'
import APIService from '../api/APIService'
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank'
import CheckBoxIcon from '@material-ui/icons/CheckBox'

const icon = <CheckBoxOutlineBlankIcon fontSize='small' />
const checkedIcon = <CheckBoxIcon fontSize='small' />
export const AnswerForm = () => {
  // const { topic } = useParams()
  const [formData, setFormData] = useState({
    answer_text: '',
    is_right: '',
  })
  const API_URL = 'http://localhost:8000/api/exams/r/django/'
  const [dataState] = CollectData(API_URL)
  // console.log(dataState)
  const { answer_text, is_right } = formData

  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
    console.log(e.target.value)
  }
  const insertAnswer = () => {
    APIService.insertAnswer({ answer_text, is_right }).then((resp) =>
      console.log('Inserted')
    )
  }
  return (
    <>
      <FormContainer
        className='events'
        style={{
          display: 'flex',
          justifyContent: 'Right',
          alignItems: 'Right',
          height: '100vh',
          width: '100vh',
        }}
      >
        <select className='form-control' onChange={(e) => onChange(e)}>
          <option>---select exam Question---</option>
          {dataState.data &&
            dataState.data.map((h, i) => (
              <option key={i} value={h.title}>
                {h.title}
              </option>
            ))}
        </select>
        <hr />
        <textarea
          type='text'
          placeholder='Answer'
          rows='5'
          name='answer_text'
          value={answer_text}
          onChange={(e) => onChange(e)}
          required
        />
        <hr />
        <div>
          <input
            // value={}
            name='answer'
            type='checkbox'
            icon={icon}
            checkedIcon={checkedIcon}
            // style={{ marginRight: 8 }}
            // checked={selected}
            onChange={(e) => onChange(e)}
            style={{
              backgroundColor: '#12ffff',
              color: '#000000',
              height: '5vh',
              width: '10vh',
            }}
          />
          <label>Is Right</label>
        </div>
        <hr /> <hr /> <hr /> <hr />
        <Button
          type='submit'
          onClick={insertAnswer}
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '10vh',
            width: '20vh',
          }}
        >
          INSERT
        </Button>
      </FormContainer>
    </>
  )
}
