import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { reset_password_confirm } from '../../actions/auth'
import { FormControl, InputLabel, Input, Button } from '@material-ui/core'

const ResetepasswordConfirm = ({ match, reset_password_confirm }) => {
  const [requestSent, setRequestSent] = useState(false)
  const [formData, setFormData] = useState({
    new_password: '',
    re_new_password: '',
  })

  const { new_password, re_new_password } = formData

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value })

  const onSubmit = (e) => {
    e.preventDefault()
    const uid = match.params.uid
    const token = match.params.token
    //Login
    reset_password_confirm(uid, token, new_password, re_new_password)
    setRequestSent(true)
  }
  //Is the request sent? then redirect to the home page of the user

  if (requestSent) {
    return <Redirect to='/' />
  }

  return (
    <div
      className='container mt-3'
      style={{
        display: 'flex',
        justifyContent: 'Right',
        alignItems: 'Right',
        height: '100vh',
      }}
    >
      <form
        onSubmit={(e) => onSubmit(e)}
        style={{ width: '50%', color: '#fff', marginLeft: '16rem' }}
      >
        <FormControl margin='normal' fullWidth style={{ color: '#fff' }}>
          <InputLabel htmlFor='name'>Email</InputLabel>
          <Input
            type='password'
            placeholder='New Password'
            name='new_password'
            value={new_password}
            onChange={(e) => onChange(e)}
            minLength='5'
            required
          />
        </FormControl>
        <div className='form-group'>
          <input
            className='form-control'
            type='password'
            placeholder='Confirm New Password'
            name='re_new_password'
            value={re_new_password}
            onChange={(e) => onChange(e)}
            minLength='5'
            required
          />
        </div>
        <button className='btn btn-primary' type='submit'>
          Reset Password
        </button>
      </form>
    </div>
  )
}
export default connect(null, { reset_password_confirm })(ResetepasswordConfirm)
