import React from 'react'
import slugify from 'react-slugify'
// import { Link } from 'react-router-dom'
//Material UI
import Container from '@material-ui/core/Container'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import CardHeader from '@material-ui/core/CardHeader'
import CssBaseline from '@material-ui/core/CssBaseline'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import { CollectData } from '../api/ConnectApi'
import APIService from '../api/APIService'
const useStyles = makeStyles((theme) => ({
  '@global': {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: 'none',
    },
  },
  appBar: {
    borderBottom: `1px solid ${theme.palette.divider}`,
  },
  toolbar: {
    flexWrap: 'wrap',
  },
  toolbarTitle: {
    flexGrow: 1,
  },
  link: {
    margin: theme.spacing(1, 1.5),
  },
  heroContent: {
    padding: theme.spacing(8, 0, 6),
  },
  cardHeader: {
    backgroundColor:
      theme.palette.type === 'light'
        ? theme.palette.grey[200]
        : theme.palette.grey[700],
  },
  cardPricing: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'baseline',
    marginBottom: theme.spacing(2),
  },
}))

const ExamSelect = () => {
  const classes = useStyles()
  const API_URL = 'http://localhost:8000/api/entrance/exams/'
  const [dataState] = CollectData(API_URL)
  console.log(dataState)
  console.log(dataState)
  // const faceRec = () => {
  //   APIService.faceRecognizer()
  // }
    const headPoseEstimation = () => {
      // APIService.accessData(topic)
      APIService.faceRecognizer()
      APIService.headPoseEstimation()
      
    }


   return (
     <>
       <Container maxWidth='md' component='main'>
         <Container
           maxWidth='sm'
           component='main'
           className={classes.heroContent}
         >
           <Typography
             component='h1'
             variant='h2'
             align='center'
             color='textPrimary'
             gutterBottom
           >
             Exams
           </Typography>
           <Typography
             variant='h5'
             align='center'
             color='textSecondary'
             component='p'
           >
             Online proctoring refers to a digital form of assessment,enabling candidates to take exams from any location.The proctored exam
             software is used during online proctoring to allow students and
             participants to take exams at a place of their choosing.It must be
             adequately reliable and cheat-proof.Proctoring an online exam is
             no longer arduous.
           </Typography>
         </Container>
         <Grid container spacing={5} alignItems='flex-end'>
           {dataState.data &&
             dataState.data.map((ex) => (
               <Grid item key={ex.title} xs={12} md={4}>
                 <Card>
                   {/* <img src={ex.image} alt="not exist" /> */}
                   <CardHeader
                     title={ex.name}
                     titleTypographyProps={{ align: 'center' }}
                     subheaderTypographyProps={{ align: 'center' }}
                     className={classes.cardHeader}
                   />
                   <CardContent>
                     <div className={classes.cardPricing}>
                       <Typography
                         component='h2'
                         variant='h6'
                         color='textPrimary'
                       >
                         {ex.description}
                       </Typography>
                     </div>
                     <ul>
                       <Typography
                         component='li'
                         variant='subtitle1'
                         align='center'
                       >
                         {ex.questions_count}
                       </Typography>
                     </ul>
                   </CardContent>
                   <CardActions>
                     <Button
                       fullWidth
                       variant='outlined'
                       color='primary'
                       onClick={headPoseEstimation}
                     >
                       <a
                        href={`/schoole/exam/${slugify(ex.name)}/`}
                         className='btn btn-info'
                         role='button'
                       >
                         Start Exam
                       </a>
                     </Button>
                   </CardActions>
                 </Card>
               </Grid>
             ))}
         </Grid>
       </Container>
     </>
   )
}

export default ExamSelect