import React from 'react'
import './App.css'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Home from './pages'
import AccountCreateLogin from './pages/accountCreateLogin'
import Article from './component/ArticleList'
import 'bootstrap/dist/css/bootstrap.min.css'
// import { CookiesProvider } from 'react-cookie'
import { Index } from './component/Sframework/index'
import { Nindex } from './component/Sframework/Nindex'
import { RandomExams } from './component/ModelExam/RandomExams'
import {Play} from './component/Exam/Play'
import Activate from './component/activateAndReset/Activate'
import ResetPassword from './component/activateAndReset/ResetPassword'
import ResetPasswordConfirm from './component/activateAndReset/ResetPasswordConfirm'
import Layout from './hocs/Layout'
import { Provider } from 'react-redux'
import store from './store/store'
import { GuardedRoute } from './GuardedRoute'
function App() {
  return (
    <React.Fragment>
      <div className='App'>
        <Provider store={store}>
          <Router>
            <Layout>
              <Switch>
                <Route exact path='/signin' component={AccountCreateLogin} />
                <Route exact path='/schoole' component={Index} />
                <Route exact path='/schoole' component={Nindex} />
                <Route
                  exact
                  path='/api/exams/r/:topic'
                  component={RandomExams}
                />
                <Route exact path='/schoole/exam/:course'component={Play} />
                <Route path='/schoolInformation' component={Article} />
                <Route exact path='/reset-password' component={ResetPassword} />
                <Route
                  exact
                  path='/password/reset/confirm/:uid/:token'
                  component={ResetPasswordConfirm}
                />
                <Route
                  exact
                  path='/activate/:uid/:token'
                  component={Activate}
                />
                <Route exact path='/' component={Home} />
              </Switch>
            </Layout>
          </Router>
        </Provider>
      </div>
    </React.Fragment>
  )
}
export default App
