import React from 'react'
import { ExamSelection } from '../../ModelExam/examSelection'
import ExamSelect from '../../Exam/examSelection'

export const Services = () => {
  return (
    <div
      className='services'
      style={{
        display: 'flex',
        justifyContent: 'Right',
        alignItems: 'Right',
        height: '100vh',
      }}
    >
    </div>
  )
}

