import axios from 'axios';
import { useEffect, useState} from 'react';


export const CollectData = (url) => {
 const [fetch, setFetching] = useState({isFetching: false});
 const [dataState, setDataState] = useState({data: []});
 const [apiurl] = useState(url);


 useEffect(() => {
   const fetchDataFromApi = async () => {
     const config = {
       headers: {
         Authorization: `JWT ${JSON.parse(localStorage.getItem('access'))}`,
         'Content-Type': 'application/json',
       },
     }
     try {
       setFetching({ isFetching: true })

       const response = await axios.get(apiurl, config)

       setDataState({ ...dataState, data: response.data })
     } catch (error) {
       setFetching({ ...fetch, isFetching: true })
     }
   }
   fetchDataFromApi()
 }, [apiurl])
return [dataState]
}
  export const InsertData = async (name) => {
    if (localStorage.getItem('access')) {
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `JWT ${JSON.parse(localStorage.getItem('access'))}`,
          Accept: 'application/json',
        },
      }
      const body = JSON.stringify({
        name,
      })
      try {
        await axios.post(
          `${process.env.REACT_APP_API_URL}/api/exams/catagory/`,
          body,
          config
        )
      } catch (err) {
        console.log('error')
      }
    } else {
      console.log('No token')
    }
  }
