import React from 'react';
import {SidebarContianer, Icon, CloseIcon,SidebarLink,SidebarMenu,SidebarWrapper,SideBtnWrapper,SidebarRoute} from './SidebarElemnts';

const Index = ({isOpen, toggle}) => {
    return (
        <>
        <SidebarContianer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink to ="about" onClick={toggle}>
                        About
                    </SidebarLink>
                    <SidebarLink to ="discovery" onClick={toggle}>
                        Discovery
                    </SidebarLink>
                    <SidebarLink to ="services" onClick={toggle}>
                        Services
                    </SidebarLink>
                    <SidebarLink to ="signup" onClick={toggle}>
                        Sign Up
                    </SidebarLink>
                </SidebarMenu>
                <SideBtnWrapper>
                    <SidebarRoute to='login' onClick={toggle}>Sign In</SidebarRoute>
                </SideBtnWrapper>
            </SidebarWrapper>
        </SidebarContianer>
        </>

    )
}

export default Index
