import React from 'react'
import { FormControl, InputLabel, Input, Button } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import APIService from '../../api/APIService'
const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}))

  const faceRec = () => {
    APIService.faceRec()
  }
  const facTrainer = () => {
      APIService.faceTrainer()
    }
    const faceRecognizer = () => {
      APIService.faceRecognizer()
    }
const Contact = () => {
  const classes = useStyles()
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'Right',
          alignItems: 'Right',
          height: '100vh',
        }}
      >
        <form style={{ width: '80%', color: '#fff', marginLeft: '16rem' }}>
          <h1>Contact Form</h1>

          <FormControl margin='normal' fullWidth style={{ color: '#fff' }}>
            <InputLabel htmlFor='name'>User name</InputLabel>
            <Input id='name' type='text' />
          </FormControl>
          <div className={classes.root}>
            <Button
              variant='contained'
              color='primary'
              size='medium'
              onClick={faceRec}
            >
              Capture Photo
            </Button>
            <Button
              variant='contained'
              color='secondary'
              size='medium'
              onClick={facTrainer}
            >
              Train face
            </Button>
            <Button
              variant='contained'
              color='default'
              size='medium'
              onClick={faceRecognizer}
            >
              Recognize
            </Button>
          </div>
        </form>
      </div>
    )
}

export default Contact
