import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { FormControl, InputLabel, Input, Button } from '@material-ui/core'
import { connect } from 'react-redux'
import { reset_password } from '../../actions/auth'

const Resetepassword = ({ reset_password }) => {
 const [requestSent, setRequestSent] = useState(false)
  const [formData, setFormData] = useState({
    email: ''
  })

  const { email } = formData

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value })

  const onSubmit = (e) => {
    e.preventDefault()
    //Login
    reset_password(email)
    setRequestSent(true)
  }
  //Is the user authenticated? then redirect to the home page of the user

  if (requestSent) {
    return <Redirect to='/' />
  }

  return (
    <div
      className='container mt-3'
      style={{
        display: 'flex',
        justifyContent: 'Right',
        alignItems: 'Right',
        height: '100vh',
      }}
    >
      <form
        style={{ width: '50%', color: '#fff', marginLeft: '16rem' }}
        onSubmit={(e) => onSubmit(e)}
      >
        <FormControl margin='normal' fullWidth style={{ color: '#fff' }}>
          <InputLabel htmlFor='name'>Email</InputLabel>
          <Input
            type='text'
            placeholder='EMail'
            name='email'
            value={email}
            onChange={(e) => onChange(e)}
            required
          />
        </FormControl>
        <Button variant='contained' color='primary' size='medium'>
          Reset Password
        </Button>
      </form>
    </div>
  )
}
export default connect(null, { reset_password })(Resetepassword)
