import React from 'react'

const Support = () => {
  return (
    <div
      className='support'
      style={{
        display: 'flex',
        justifyContent: 'Right',
        alignItems: 'Right',
        height: '100vh',
      }}
    >
      <h1>Support us</h1>
    </div>
  )
}

export default Support
