import React, { useState, useContext } from 'react'
import { BoxContainer } from '../AccountBoxElement/AccountBoxElement'
import { Button } from '../ButtonElements'
import { CollectData } from '../api/ConnectApi'
import { InsertData } from '../api/ConnectApi'
import { FormContainer } from '../AccountBoxElement/Common'
import { Link, Redirect, useParams } from 'react-router-dom'
import APIService from '../api/APIService'

export const QuestionForm = () => {
  // const { topic } = useParams()
  // // console.log(topic)
  const [formData, setFormData] = useState({
    title: '',
    category: '',
  })
  const API_URL = 'http://localhost:8000/api/exams/ex/'
  const [dataState] = CollectData(API_URL)
  // console.log(dataState)
  const { title, category } = formData

  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
    console.log(e.target.value)
  }
  const insertQuestion = () => {
    APIService.insertQuestion(title, category).then((resp) =>
      console.log('Inserted')
    )
  }

  return (
    <>
      <FormContainer
        className='events'
        style={{
          display: 'flex',
          justifyContent: 'Right',
          alignItems: 'Right',
          height: '100vh',
          width: '100vh',
        }}
      >
        <textarea
          type='text'
          placeholder='Question'
          rows='5'
          name='title'
          value={title}
          onChange={(e) => onChange(e)}
          required
        />
        <hr />
        <select
          className='form-control'
          name='category'
          onChange={(e) => onChange(e)}
        >
          <option>---select exam catagory---</option>
          {dataState.data &&
            dataState.data.map((h, i) => (
              <option key={i} value={h.title}>
                {h.title}
              </option>
            ))}
        </select>
        <hr /> <hr /> <hr /> <hr />
        <Button
          type='submit'
          onClick={insertQuestion}
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '10vh',
            width: '20vh',
          }}
        >
          INSERT
        </Button>
      </FormContainer>
    </>
  )
}
