import React from 'react'

export const Events = () => {
  return (
    <div
      className='events'
      style={{
        display: 'flex',
        justifyContent: 'Right',
        alignItems: 'Right',
        height: '100vh',
      }}
    >
      {/* <h1>Events</h1> */}
    </div>
  )
}

