import React, { Component, Fragment } from 'react'
import { Helmet } from 'react-helmet'
import AccessAlarmIcon from '@material-ui/icons/AccessAlarm'
import './play.css'
import slugify from 'react-slugify'
import Sidebar from '../Sframework/Sidebar/Sidebar'
import Footer from '../Footer/index'
import classNames from 'classnames/bind'
import isEmpty from './is-Empty'
// import questions from './questions.json'
export class Play extends Component {
  constructor(props) {
    super(props)
    this.state = {
      // questions,
      currentQuestion: {},
      nextQuestion: {},
      previousQuestion: {},
      dataSet: [],
      answer: false,
      numberOfQuestions: 0,
      numberOfAnsweredQuestions: 0,
      currentQuestionIndex: 0,
      score: 0,
      correctAnswers: 0,
      wrongAnswers: 0,
      nextButtonDisabled: false,
      previousButtonDisabled: true,
      time: {},
    }
    this.interval = null
  }


  componentDidMount() {
    const course = this.props.match.params.course
    console.log(course)
        fetch(`http://localhost:8000/api/entrance/exams/${course}`, {
          // method: 'GET',
          headers: {
            Authorization: `JWT ${JSON.parse(localStorage.getItem('access'))}`,
            'Content-Type': 'application/json; charset=utf8',
          },
        })
          .then((response) => response.json())
          .then((data) => {
            this.setState({ dataSet: data })
            const { dataSet, currentQuestion, nextQuestion, previousQuestion } =
              this.state
            this.displayQuestions(
              dataSet,
              currentQuestion,
              nextQuestion,
              previousQuestion
            )
            this.totalQuestion()
            this.startTimer()
          })
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }
  totalQuestion = () => {
    const course = this.props.match.params.course
    fetch(`http://localhost:8000/api/entrance/exams/`, {
      // method: 'GET',
      headers: {
        Authorization: `JWT ${JSON.parse(localStorage.getItem('access'))}`,
        'Content-Type': 'application/json; charset=utf8',
      },
    }).then((response) => response.json())
          .then((data) => {
            data.map((res)=>{
              if (slugify(res.name) === course) 
              {
                this.setState({ numberOfQuestions: res.questions_count })
                console.log(this.state.numberOfQuestions)
              }
            })
          })
  }

  displayQuestions = (
    dataSet,
    currentQuestion,
    nextQuestion,
    previousQuestion
  ) => {
    let { currentQuestionIndex } = this.state
    if (this.state.dataSet.exam && !isEmpty(this.state.dataSet.exam)) {
      dataSet = this.state.dataSet.exam.question_set
      console.log(this.state.dataSet)
      currentQuestion = dataSet[currentQuestionIndex]
      nextQuestion = dataSet[currentQuestionIndex + 1]
      previousQuestion = dataSet[currentQuestionIndex - 1]
      this.setState(
        {
          currentQuestion,
          nextQuestion,
          previousQuestion,
        },
        () => {
          this.handleDisableButton()
        }
      )
    }
  }
  
  handleOptionClick = (e) => {
    const {currentQuestion} = this.state
     currentQuestion.answer_set.map((val) => {
    if (e.target.innerHTML.toLowerCase() === val.label.toLowerCase() && val.is_correct === true) {
      this.correctAnswer()
    } else if (
      e.target.innerHTML.toLowerCase() !== val.label.toLowerCase()
    ) {
      // console.log("doing nothing!")
    }
    else{
      this.wrongAnswer()
    }
     })
  }

  handleNextButtonClick = () => {
    if (this.state.nextQuestion !== undefined) {
      this.setState(
        (prevState) => ({
          currentQuestionIndex: prevState.currentQuestionIndex + 1,
        }),
        () => {
          this.displayQuestions(
            this.state.dataSet.exam.question_set,
            this.state.currentQuestion,
            this.state.nextQuestion,
            this.state.previousQuestion
          )
        }
      )
    }
  }

  handlePreviousButtonClick = () => {
    if (this.state.previousQuestion !== undefined) {
      this.setState(
        (prevState) => ({
          currentQuestionIndex: prevState.currentQuestionIndex - 1,
        }),
        () => {
          this.displayQuestions(
            this.state.dataSet.exam.question_set,
            this.state.currentQuestion,
            this.state.nextQuestion,
            this.state.previousQuestion
          )
        }
      )
    }
  }

  handleButtonClick = (e) => {
    switch (e.target.id) {
      case 'next-button':
        this.handleNextButtonClick()
        break
      case 'previous-button':
        this.handlePreviousButtonClick()
        break
      default:
        break
    }
  }
  correctAnswer = (e) => {
    console.log('correct')
    this.setState(
      (prevState) => ({
        score: prevState.score + 1,
        correctAnswers: prevState.correctAnswers + 1,
        currentQuestionIndex: prevState.currentQuestionIndex + 1,
        numberOfAnsweredQuestions: prevState.numberOfAnsweredQuestions + 1,
      }),
      () => {
        if (this.state.nextQuestion === undefined) {
          this.result()
        } else {
          this.displayQuestions(
            this.state.dataSet.exam.question_set,
            this.state.currentQuestion,
            this.state.nextQuestion,
            this.state.previousQuestion
          )
        }
      }
    )
  }

  wrongAnswer = (e) => {
    console.log('wrong')
    this.setState(
      (prevState) => ({
        wrongAnswers: prevState.wrongAnswers + 1,
        currentQuestionIndex: prevState.currentQuestionIndex + 1,
        numberOfAnsweredQuestions: prevState.numberOfAnsweredQuestions + 1,
      }),
      () => {
        if (this.state.nextQuestion === undefined) {
          this.result()
        } else {
          this.displayQuestions(
            this.state.dataSet.exam.question_set,
            this.state.currentQuestion,
            this.state.nextQuestion,
            this.state.previousQuestion
          )
        }
      }
    )
  }

  startTimer = () => {
    const countDownTime = Date.now() + 600000
    this.interval = setInterval(() => {
      const now = new Date()
      const duration = countDownTime - now
      const minutes = Math.floor((duration % (1000 * 60 * 60)) / (1000 * 60))
      const seconds = Math.floor((duration % (1000 * 60)) / 1000)
      if (duration < 0) {
        clearInterval(this.interval)
        this.setState(
          {
            time: {
              minutes: 0,
              seconds: 0,
            },
          },
          () => {
            this.result()
          }
        )
      } else {
        this.setState({
          time: {
            minutes,
            seconds,
          },
        })
      }
    }, 1000)
  }

  handleDisableButton = () => {
    if (
      this.state.previousQuestion === undefined ||
      this.state.currentQuestionIndex === 0
    ) {
      this.setState({
        previousButtonDisabled: true,
      })
    } else {
      this.setState({
        previousButtonDisabled: false,
      })
    }
    if (
      this.state.nextQuestion === undefined ||
      this.state.currentQuestionIndex + 1 === this.state.numberOfQuestions
    ) {
      this.setState({
        nextButtonDisabled: true,
      })
    } else {
      this.setState({
        nextButtonDisabled: false,
      })
    }
  }

  result = () => {
    alert('Exam has ended')
    const { state } = this
    const ExamTakerResult = {
      score: state.score,
      numberOfQuestions:state.numberOfQuestions,
      numberOfAnsweredQuestions: state.numberOfAnsweredQuestions,
      correctAnswers: state.correctAnswers,
      wrongAnswers: state.wrongAnswers,
    }
    console.log(ExamTakerResult)
    setTimeout(() => {
      this.props.history.push('/schoole')
    }, 1000)
  }
  render() {
    const {
      currentQuestion,
      numberOfQuestions,currentQuestionIndex,
      dataSet,
      time,
    } = this.state
      // console.log(this.state.currentQuestion)
    return (
      <Fragment>
        <Helmet>
          <title>Entrance Exam</title>
        </Helmet>
        <Sidebar />
        <div
          className='question'
          style={{
            height: '150vh',
          }}
        >
          <div>
            <p>
              <span className='left' style={{ float: 'left' }}>
                {currentQuestionIndex + 1} of {numberOfQuestions}
              </span>
              <br />
              <span className='right' style={{ float: 'right' }}>
                {time.minutes}:{time.seconds}
                <AccessAlarmIcon />
              </span>
            </p>
          </div>
          <h4>{currentQuestion.label}</h4>
          <div className='options-container'>
            {currentQuestion.answer_set &&
              currentQuestion.answer_set.map((res) => (
                <p onClick={this.handleOptionClick} className='option'>
                  {res.label}
                </p>
              ))}
          </div>
          <div className='button-container'>
            <button
              className={classNames('', {
                disable: this.state.previousButtonDisabled,
              })}
              id='previous-button'
              onClick={this.handleButtonClick}
            >
              Previous
            </button>
            <button
              className={classNames('', {
                disable: this.state.nextButtonDisabled,
              })}
              id='next-button'
              onClick={this.handleButtonClick}
            >
              Next
            </button>
          </div>
        </div>
        {/* ))} */}
        <Footer />
      </Fragment>
    )
  }
}

