import React, { useState} from 'react'
import {Button} from '../ButtonElements'
// import {InsertData} from '../api/ConnectApi'
import APIService from '../api/APIService'
import { FormContainer } from '../AccountBoxElement/Common'

export const CatagoryForm = () => {
  const [formData, setFormData] = useState({
    name: '',
  })
  const { name } = formData
 const insertCatagory = () => {
   APIService.insertCatagory({ name}).then((resp) =>console.log("created")
   )
 }
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }

  return (
    <>
        <FormContainer
          className='events'
          style={{
            display: 'flex',
            justifyContent: 'Right',
            alignItems: 'Right',
            height: '100vh',
            width: '150vh',
          }}
        >
          <textarea
            type='text'
            placeholder='Catagory'
            rows='3'
            name='name'
            value={name}
            onChange={(e) => onChange(e)}
            required
          />
          <hr /> <hr /> <hr /> <hr /> <hr /> <hr />
          <hr /> <hr />
          <Button
            type='submit'
            onClick={insertCatagory}
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              height: '10vh',
              width: '20vh',
            }}
          >
            INSERT
          </Button>
        </FormContainer>
    </>
  )
}
