import React from 'react'

export const AboutUs = () => {
  return (
    <>
      <div
        className='home'
        style={{
          display: 'flex',
          justifyContent: 'Right',
          alignItems: 'Right',
          height: '100vh',
        }}
      >
      </div>
    </>
  )
}

