import styled from 'styled-components';
import { motion } from "framer-motion";
export const BoxContainer = styled.div`
  width: 380px;
  min-height: 550px;
  display: flex;
  flex-direction: column;
  border-radius: 19px;
  box-shadow: 0 0 8px rgba(15, 15, 15, 0.28);
  position: relative;
  overflow: hidden;
  align-items: center;
  margin-left: 150px;
`;
export const TopContainer = styled.div`
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 1.8em;
  padding-bottom: 5em;
  align-items: center;
  margin-right: 10px;
`;
export const HeaderContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 150px;
  margin-left: 150px;
`;
export const HeaderText = styled.h1`
  font-size: 30px;
  font-weight: 600;
  line-height: 1.04;
  color: #fff;
  z-index: 1;
  margin: 0;
  margin-top: 150px;
  margin-left: 10px;
`;
export const SmallText = styled.h5`
  color: #fff;
  font-weight: 600;
  font-size: 11px;
  z-index: 10;
  margin: 0;
  margin-top: 30px;
  margin-left: 10px;
`;
export const BackDrop = styled(motion.div)`
  width: 160%;
  height: 550px;
  position: absolute;
  display: flex;
  flex-direction: column;
  border-radius: 50%;
  transform: rotate(60deg);
  top: -290px;
  left: -70px;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
`;
export const InnerContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: -8em;
  margin-right: 300px;
  margin-top:-70px;
`;
export const backdropVariants = {
  expanded: {
    width: '233%',
    height: '1050px',
    borderRadius: '20%',
    transform: 'rotate(60deg)',
  },
  collapsed: {
    width: '160%',
    height: '550px',
    borderRadius: '50%',
    transform: 'rotate(0deg)',
  },
}
export const expandingTransition = {
  type: 'spring',
  duration: 2.3,
  stiffness: 30,
}

