import React, { useState, useEffect } from 'react'
import './index.css'
import EditForm from './EditForm'
import APIService from '../api/APIService'
import { useCookies } from 'react-cookie'

function ArticleList() {
  const [articles, setArticles] = useState([])
  const [editArticle, setEditArticle] = useState(null)
  const [token] = useCookies(['csrftoken'])

  useEffect(() => {
    fetch('http://127.0.0.1:8000/api/Info/articles/', {
      method: 'GET',
      headers: {
        'Content-Type': 'aplication/json',
        'Authorization': `JWT ${JSON.parse(localStorage.getItem('access'))}`,
      },
    })
      .then((resp) => resp.json())
      .then((resp) => setArticles(resp))
      .catch((error) => console.log(error))
  }, [])

  const editBtn = (article) => {
    setEditArticle(article)
  }
  const Deletion = (article)=> {
    const new_article = articles.filter((myarticle) => {
      if (myarticle.id === article.id) {
        return false
      } else {
        return true
      }
      
    })
    setArticles(new_article)
  }

  const deleteBtn = (article) => {
    APIService.DeleteArticle(article.id, token['csrftoken'])
      .then(() => Deletion(article))
      .catch((error) => console.log(error))
  }

  const articleForm = () => {
    setEditArticle({ title: '', description: '' })
  }

  const updatedInformation = (article) => {
    const new_article = articles.map((myarticle) => {
      if (myarticle.id === article.id) {
        return article
      } else {
        return myarticle
      }
    })

    setArticles(new_article)
  }

  const insertedInformation = (article) => {
    const new_article = [...articles, article]
    setArticles(new_article)
  }

  return (
    <div className='Index'>
      <div className='row'>
        <div className='col'>
          <h3>Django And ReactJS Integration</h3>
          <br />
          <br />
        </div>
        <div className='col'>
          <button onClick={articleForm} className='btn btn-primary'>
            Insert
          </button>
        </div>
      </div>
      {articles && articles.length > 0?
      articles.map((article) => {
          return (
            <div key={article.id}>
              <h2>{article.title}</h2>
              <br />
              <hr className='hrclass' />
              <p>{article.description}</p>
              <div className='row'>
                <div className='col-mod-1'>
                  <button
                    className='btn btn-primary'
                    onClick={() => editBtn(article)}
                  >
                    Update
                  </button>
                </div>
                <div className='col'>
                  <button onClick={() => deleteBtn(article)} className='btn btn-danger'>
                    Delete
                  </button>
                </div>
              </div>
              <hr className='hrclass' />
            </div>
          )
        }):<span></span>
        }
      {editArticle ? (
        <EditForm
          article={editArticle}
          updatedInformation={updatedInformation}
          insertedInformation={insertedInformation}
        />
      ) : null}
    </div>
  )
}

export default ArticleList
