import React,{useState} from 'react'
import {
  BackDrop,
  BoxContainer,
  HeaderContainer,
  HeaderText,
  InnerContainer,
  SmallText,
  TopContainer,
  backdropVariants,
  expandingTransition,
} from './AccountBoxElement'
import { AccountContext } from './accountContext';
import LogIns from './logInForm'
import SignUps from './signUpForm'


export function AccountBox(props) {
const [isExpended, setExpended] = useState(false);
const [active, setActive] = useState("signin");
  const playTransitionAnimation = () => {
    setExpended(true);
    setTimeout(() => {
      setExpended(false)
    }, expandingTransition.duration * 1000);
  };

  const switchToSignup = () => {
    playTransitionAnimation();
    setTimeout(() => {
    setActive("signup");
    }, 400);
  };
    const switchToSignin = () => {
      playTransitionAnimation()
      setTimeout(() => {
        setActive('signin');
      }, 1000)
    };
  const contextValue = {switchToSignup, switchToSignin }

  return (
    <>
      <AccountContext.Provider value={contextValue}>
        <BoxContainer>
          <TopContainer>
            <BackDrop
              initial={false}
              animate={isExpended ? 'expanded' : 'collapsed'}
              variants={backdropVariants}
              transition={expandingTransition}
            >
              {active === 'signin' && (
                <HeaderContainer>
                  <HeaderText>Well Come Back</HeaderText>
                  <SmallText>Please Sign-In to continoue</SmallText>
                </HeaderContainer>
              )}
              {active === 'signup' && (
                <HeaderContainer>
                  <HeaderText>Create Account</HeaderText>
                  <SmallText>Please Sign-Up to continoue</SmallText>
                </HeaderContainer>
              )}
            </BackDrop>
          </TopContainer>
          <InnerContainer>
            {active === 'signin' && <LogIns />}
            {active === 'signup' && <SignUps />}
          </InnerContainer>
        </BoxContainer>
      </AccountContext.Provider>
    </>
  )
}
export default AccountBox
