import React, { useState } from 'react';
import { Button } from '../ButtonElements';
import {HeroContainer, 
    HeroBg,
    VidoeBg,
    HeroContent ,
    HeroH1, 
    HeroP,
    HeroBtnWrapper,
    ArrowForward ,
    ArrowRight } from './HeroElements';
import Video from '../../videos/video.mp4';

const Index = () => {
    const [hover, setHover] = React.useState(false);
    const onHover = ()=>{
        setHover(!hover);
    }

    return (
        <>
        <HeroContainer id='home' >
            <HeroBg>
                <VidoeBg autoPlay loop muted src={Video} 
                type='video/mp4'/>
            </HeroBg>
            <HeroContent>
            <HeroH1>Virtual Learning and Online Examination System</HeroH1>
            <HeroP>Hey!wake up lets read and create amaizing world.</HeroP>
            <HeroBtnWrapper>
                <Button to ='signup' onMouseEnter={onHover}
                onMouseLeave={onHover}
                primary='true'
                dark='true'
                smooth={true} 
                duration={500} 
                spy={true} 
                exact='true' 
                offset={-80}
                >
                Get started {hover ? <ArrowForward /> : <ArrowRight/>}
                </Button>
            </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
        </>
    );
};

export default Index;